
import type { Event, Volunteer } from '@/types';
import ScheduleItemCard from './ScheduleItemCard';

interface MyScheduleListProps {
  assignments: Event[];
  volunteerId: string;
}

export default function MyScheduleList({ assignments, volunteerId }: MyScheduleListProps) {
  if (assignments.length === 0) {
    return (
      <div className="text-center py-16 border-2 border-dashed rounded-lg bg-card">
        <h2 className="text-xl font-semibold">No assignments yet!</h2>
        <p className="text-muted-foreground mt-2">When you're assigned to a role, it will appear here.</p>
      </div>
    );
  }

  const upcomingAssignments = assignments.filter(event => event.eventDate >= new Date());
  const pastAssignments = assignments.filter(event => event.eventDate < new Date()).sort((a, b) => b.eventDate.getTime() - a.eventDate.getTime());

  return (
    <div className="space-y-8">
      <div>
        <h2 className="text-2xl font-headline font-bold mb-4">Upcoming</h2>
        <div className="space-y-4">
          {upcomingAssignments.length > 0 ? (
            upcomingAssignments.map(event => {
                const myRole = event.roles.find(r => r.assignedVolunteerId === volunteerId);
                return myRole ? (
                  <ScheduleItemCard 
                    key={`${event.id}-${myRole.id}`}
                    event={event}
                    role={myRole}
                  />
                ) : null;
             })
          ) : (
             <p className="text-muted-foreground mt-2">No upcoming assignments.</p>
          )}
        </div>
      </div>
      
      <div>
        <h2 className="text-2xl font-headline font-bold mb-4">Past</h2>
        <div className="space-y-4">
          {pastAssignments.length > 0 ? (
             pastAssignments.map(event => {
                const myRole = event.roles.find(r => r.assignedVolunteerId === volunteerId);
                return myRole ? (
                  <ScheduleItemCard 
                    key={`${event.id}-${myRole.id}`}
                    event={event}
                    role={myRole}
                  />
                ) : null;
             })
          ) : (
            <p className="text-muted-foreground mt-2">No past assignments.</p>
          )}
        </div>
      </div>
    </div>
  );
}
