
'use client';
import { useState } from 'react';
import { format, isPast } from 'date-fns';
import { Card, CardContent, CardHeader, CardTitle, CardDescription, CardFooter } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Calendar, Clock, User, MailQuestion, Check, X } from 'lucide-react';
import type { Event, Role } from '@/types';
import { cn } from '@/lib/utils';
import RequestChangeModal from './RequestChangeModal';
import { generateRequestEmail } from '@/ai/flows/generate-request-email-flow';
import { useAuth } from '@/hooks/use-auth';
import { useToast } from '@/hooks/use-toast';

interface ScheduleItemCardProps {
  event: Event;
  role: Role;
}

export default function ScheduleItemCard({ event, role }: ScheduleItemCardProps) {
  const { dataService, churchId } = useAuth();
  const { toast } = useToast();
  const [isModalOpen, setIsModalOpen] = useState(false);
  const eventIsInPast = isPast(event.eventDate);

  const handleStatusUpdate = async (status: 'Confirmed' | 'Declined') => {
    if (!dataService || !churchId) return;

    try {
        await dataService.updateRoleInEvent(churchId, event.id, role.id, { status });
        toast({
            title: `Assignment ${status}`,
            description: `You have ${status.toLowerCase()} your role for ${event.eventName}.`
        });
    } catch(e) {
        toast({
            variant: 'destructive',
            title: 'Error',
            description: 'Could not update your status. Please try again.'
        });
    }
  };

  return (
    <>
    <Card className={cn(
        "transition-all", 
        eventIsInPast && "opacity-60 bg-muted/50",
        role.status === 'Pending' && !eventIsInPast && "border-primary border-2",
        role.status === 'Declined' && "bg-destructive/10 border-destructive/50"
    )}>
      <CardHeader>
        <div className="flex justify-between items-start">
            <div>
                <CardTitle className="font-headline text-xl">{event.eventName}</CardTitle>
                <CardDescription className="flex flex-wrap items-center gap-x-4 gap-y-1 text-sm mt-1">
                    <span className="flex items-center gap-1.5"><Calendar className="h-4 w-4 text-primary" /> {format(event.eventDate, 'EEEE, MMM d')}</span>
                    <span className="flex items-center gap-1.5"><Clock className="h-4 w-4 text-primary" /> {format(event.eventDate, 'p')}</span>
                </CardDescription>
            </div>
            {!eventIsInPast && (
                 <div className={cn(
                    "text-xs font-bold uppercase px-2 py-1 rounded-full",
                    role.status === 'Pending' && 'bg-accent text-accent-foreground',
                    role.status === 'Confirmed' && 'bg-primary text-primary-foreground',
                    role.status === 'Declined' && 'bg-destructive text-destructive-foreground',
                 )}>
                    {role.status}
                </div>
            )}
        </div>
      </CardHeader>
      <CardContent>
        <div className="flex items-center gap-2 bg-background p-3 rounded-md border">
            <User className="h-5 w-5 text-primary" />
            <div>
                <p className="text-sm text-muted-foreground">Your Role</p>
                <p className="font-semibold">{role.roleName}</p>
            </div>
        </div>
      </CardContent>
      {!eventIsInPast && (
        <CardFooter className="flex justify-end gap-2">
            {role.status === 'Pending' ? (
                <>
                    <Button onClick={() => handleStatusUpdate('Declined')} variant="destructive">
                        <X className="mr-2 h-4 w-4" /> Decline
                    </Button>
                    <Button onClick={() => handleStatusUpdate('Confirmed')}>
                        <Check className="mr-2 h-4 w-4" /> Accept
                    </Button>
                </>
            ) : (
                <Button 
                    onClick={() => setIsModalOpen(true)}
                    variant="outline"
                    disabled={role.status === 'Declined'}
                >
                    <MailQuestion className="mr-2 h-4 w-4" />
                    Request Change
                </Button>
            )}
        </CardFooter>
      )}
    </Card>
    <RequestChangeModal
      isOpen={isModalOpen}
      onClose={() => setIsModalOpen(false)}
      event={event}
      role={role}
      sendRequestAction={generateRequestEmail}
    />
    </>
  );
}
