
'use client';

import { useState, useEffect } from 'react';
import { useForm } from 'react-hook-form';
import { zodResolver } from '@hookform/resolvers/zod';
import * as z from 'zod';
import { Button } from '@/components/ui/button';
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogFooter, DialogDescription } from '@/components/ui/dialog';
import { Form, FormControl, FormField, FormItem, FormLabel, FormMessage } from '@/components/ui/form';
import { Textarea } from '@/components/ui/textarea';
import { useToast } from '@/hooks/use-toast';
import { Loader2, Send } from 'lucide-react';
import type { Event, Role, Church } from '@/types';
import { RadioGroup, RadioGroupItem } from '../ui/radio-group';
import type { GenerateRequestEmailInput, GenerateRequestEmailOutput } from '@/ai/flows/generate-request-email-flow';
import { sendEmail } from '@/lib/email';
import { useAuth } from '@/hooks/use-auth';

const requestFormSchema = z.object({
  requestType: z.enum(['replacement', 'trade'], {
    required_error: 'You need to select a request type.',
  }),
  message: z.string().optional(),
});

type RequestFormValues = z.infer<typeof requestFormSchema>;

interface RequestChangeModalProps {
  isOpen: boolean;
  onClose: () => void;
  event: Event;
  role: Role;
  sendRequestAction: (input: GenerateRequestEmailInput) => Promise<GenerateRequestEmailOutput>;
}

export default function RequestChangeModal({ isOpen, onClose, event, role, sendRequestAction }: RequestChangeModalProps) {
  const { dataService, churchId } = useAuth();
  const { toast } = useToast();
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [church, setChurch] = useState<Church | null>(null);

  const form = useForm<RequestFormValues>({
    resolver: zodResolver(requestFormSchema),
  });
  
  useEffect(() => {
    if (!dataService || !churchId || !isOpen) return;
    const unsubscribe = dataService.getChurch(churchId, setChurch);
    return () => unsubscribe();
  }, [dataService, churchId, isOpen]);


  const onSubmit = async (data: RequestFormValues) => {
    if (!church) return;
    setIsSubmitting(true);
    try {
      const result = await sendRequestAction({
          eventName: event.eventName,
          eventDate: event.eventDate.toISOString(),
          roleName: role.roleName,
          requestType: data.requestType,
          volunteerName: role.assignedVolunteerName || 'A Volunteer',
          message: data.message,
      });

      const adminEmail = church?.adminEmail || 'admin@example.com';
      
      await sendEmail({
        to: adminEmail, // Sending to admin for demo purposes
        subject: result.emailSubject,
        htmlBody: result.emailBody,
      });
      
      toast({
        title: 'Request Sent',
        description: 'Your request has been sent for consideration.',
      });
      form.reset();
      onClose();
    } catch (error) {
      console.error("Error sending request:", error);
      toast({
        variant: 'destructive',
        title: 'Error',
        description: 'Failed to send request. Please try again.',
      });
    } finally {
      setIsSubmitting(false);
    }
  };

  return (
    <Dialog open={isOpen} onOpenChange={onClose}>
      <DialogContent className="sm:max-w-[480px]">
        <DialogHeader>
          <DialogTitle className="font-headline">Request Change</DialogTitle>
          <DialogDescription>
            Request a replacement or a date trade for your role as <strong>{role.roleName}</strong> on <strong>{event.eventName}</strong>.
          </DialogDescription>
        </DialogHeader>
        <Form {...form}>
          <form onSubmit={form.handleSubmit(onSubmit)} className="space-y-4 py-4">
            <FormField
              control={form.control}
              name="requestType"
              render={({ field }) => (
                <FormItem className="space-y-3">
                  <FormLabel>What would you like to do?</FormLabel>
                  <FormControl>
                    <RadioGroup
                      onValueChange={field.onChange}
                      defaultValue={field.value}
                      className="flex flex-col space-y-1"
                    >
                      <FormItem className="flex items-center space-x-3 space-y-0">
                        <FormControl>
                          <RadioGroupItem value="replacement" />
                        </FormControl>
                        <FormLabel className="font-normal">
                          Find a Replacement
                        </FormLabel>
                      </FormItem>
                      <FormItem className="flex items-center space-x-3 space-y-0">
                        <FormControl>
                          <RadioGroupItem value="trade" />
                        </FormControl>
                        <FormLabel className="font-normal">
                          Request a Trade
                        </FormLabel>
                      </FormItem>
                    </RadioGroup>
                  </FormControl>
                  <FormMessage />
                </FormItem>
              )}
            />
            <FormField
              control={form.control}
              name="message"
              render={({ field }) => (
                <FormItem>
                  <FormLabel>Message (Optional)</FormLabel>
                  <FormControl>
                    <Textarea placeholder="Add a personal note to your request..." {...field} />
                  </FormControl>
                  <FormMessage />
                </FormItem>
              )}
            />
            <DialogFooter>
              <Button type="button" variant="ghost" onClick={onClose} disabled={isSubmitting}>Cancel</Button>
              <Button type="submit" disabled={isSubmitting}>
                {isSubmitting ? <Loader2 className="mr-2 h-4 w-4 animate-spin" /> : <Send className="mr-2 h-4 w-4" />}
                Send Request
              </Button>
            </DialogFooter>
          </form>
        </Form>
      </DialogContent>
    </Dialog>
  );
}
