
'use server';
/**
 * @fileOverview Generates an email body for a volunteer change request.
 * 
 * - generateRequestEmail - A function that handles generating the email.
 * - GenerateRequestEmailInput - The input type for the function.
 * - GenerateRequestEmailOutput - The return type for the function.
 */

import { ai } from '@/ai/genkit';
import { z } from 'zod';

const GenerateRequestEmailInputSchema = z.object({
    eventName: z.string().describe('The name of the event.'),
    eventDate: z.string().datetime().describe('The ISO 8601 string of the event date.'),
    roleName: z.string().describe('The name of the role needing a change.'),
    volunteerName: z.string().describe('The name of the volunteer making the request.'),
    requestType: z.enum(['replacement', 'trade']).describe('The type of request being made.'),
    message: z.string().optional().describe('An optional personal message from the volunteer.'),
});
export type GenerateRequestEmailInput = z.infer<typeof GenerateRequestEmailInputSchema>;

const GenerateRequestEmailOutputSchema = z.object({
  emailSubject: z.string().describe('The subject line for the email.'),
  emailBody: z.string().describe('The HTML body of the email.'),
});
export type GenerateRequestEmailOutput = z.infer<typeof GenerateRequestEmailOutputSchema>;


export async function generateRequestEmail(input: GenerateRequestEmailInput): Promise<GenerateRequestEmailOutput> {
  const prompt = ai.definePrompt({
    name: 'generateRequestEmailPrompt',
    input: { schema: GenerateRequestEmailInputSchema },
    output: { schema: GenerateRequestEmailOutputSchema },
    prompt: `You are an assistant for a church volunteer scheduling system called "RotaScribe". Your task is to generate a clear, friendly, and professional email to be sent to other volunteers about a schedule change request.

The request is from: {{{volunteerName}}}
Event: {{{eventName}}}
Date: {{{eventDate}}}
Role: {{{roleName}}}
Request Type: {{{requestType}}}
{{#if message}}
Personal Message: {{{message}}}
{{/if}}

- The email subject should be concise and informative. For a replacement, use "Replacement needed for [Role Name] on [Event Name]". For a trade, use "Trade request for [Role Name] on [Event Name]".
- The email body should be in HTML format.
- Start with a friendly greeting.
- Clearly state who is making the request, for what event, date, and role.
- Based on the requestType, explain what is needed:
  - For a 'replacement', ask if anyone is available to fill in for the role.
  - For a 'trade', ask if anyone is willing to trade for the same role on a different date.
- If a personal message is included, make sure to add it in a quoted section.
- End with a clear call to action, like "Please reply to this email if you can help."
- Sign off warmly from "RotaScribe Scheduling".`,
  });

  const { output } = await prompt(input);
  return output!;
}
