
'use client';
import { Sidebar, SidebarContent, SidebarGroup, SidebarMenu, SidebarMenuItem, SidebarMenuButton, useSidebar } from "@/components/ui/sidebar"
import { useAuth } from "@/hooks/use-auth";
import { BookCopy, Calendar, CalendarCheck, Settings, Share2, ShieldCheck, UserCog, Users } from "lucide-react";
import Link from "next/link";
import { usePathname } from "next/navigation";
import { useEffect, useState } from "react";
import type { Volunteer } from "@/types";

const AppSidebar = () => {
    const pathname = usePathname();
    const { isAdmin, userId, churchId, dataService } = useAuth();
    const { setOpenMobile } = useSidebar();
    const [familyManagementEnabled, setFamilyManagementEnabled] = useState(false);

    useEffect(() => {
        if (!dataService || !churchId || !userId || isAdmin) {
            setFamilyManagementEnabled(false);
            return;
        };

        const unsub = dataService.getVolunteer(churchId, userId, (volunteer: Volunteer | null) => {
            if (volunteer) {
                setFamilyManagementEnabled(volunteer.preferences.familyManagementEnabled);
            }
        });

        // This is a bit of a hack to listen for preference changes on the mock db
        const handleStorageChange = () => {
             if (!dataService || !churchId || !userId || isAdmin) return;
             dataService.getVolunteer(churchId, userId, (volunteer: Volunteer | null) => {
                if (volunteer) {
                    setFamilyManagementEnabled(volunteer.preferences.familyManagementEnabled);
                }
            });
        }
        window.addEventListener('storage', handleStorageChange);

        return () => {
            unsub();
            window.removeEventListener('storage', handleStorageChange);
        }

    }, [dataService, churchId, userId, isAdmin]);


    const baseNavLinks = [
        { href: '/my-schedule', label: 'My Schedule', icon: Calendar },
        { href: '/my-availability', label: 'My Availability', icon: CalendarCheck },
        { href: '/my-preferences', label: 'My Preferences', icon: Settings },
    ];

    if (familyManagementEnabled && !isAdmin) {
        baseNavLinks.push({ href: '/my-family', label: 'My Family', icon: UserCog });
    }

    const navLinks = isAdmin
        ? [
            { href: '/admin', label: 'Admin Console', icon: ShieldCheck },
            { href: '/admin/services', label: 'Services', icon: Calendar },
            { href: '/admin/volunteers', label: 'Volunteers', icon: Users },
            { href: '/admin/templates', label: 'Templates', icon: BookCopy },
            { href: '/admin/share', label: 'Share', icon: Share2 },
            { href: '/admin/settings', label: 'Settings', icon: Settings },
        ]
        : baseNavLinks;
    
    return (
        <Sidebar>
            <SidebarContent>
            <SidebarGroup>
                <SidebarMenu>
                {navLinks.map(({ href, label, icon: Icon }) => (
                    <SidebarMenuItem key={href}>
                        <SidebarMenuButton asChild isActive={pathname.startsWith(href) && (href !== '/admin' || pathname === '/admin')} tooltip={label}>
                            <Link href={href} onClick={() => setOpenMobile(false)}>
                                <Icon />
                                <span>{label}</span>
                            </Link>
                        </SidebarMenuButton>
                    </SidebarMenuItem>
                ))}
                </SidebarMenu>
            </SidebarGroup>
            </SidebarContent>
        </Sidebar>
    );
};


export default function AppShell({ children }: { children: React.ReactNode }) {

    return (
        <div className="flex">
            <AppSidebar />
            <main className="flex-1">
                {children}
            </main>
        </div>
    )
}
