/**
 * @fileoverview An email service for sending emails via Resend.
 */
'use server';

import { Resend } from 'resend';

const resend = new Resend(process.env.RESEND_API_KEY);

/**
 * Sends an email using the Resend service.
 * @param to The recipient's email address.
 * @param subject The subject line of the email.
 * @param htmlBody The HTML content of the email body.
 */
export async function sendEmail({ to, subject, htmlBody }: { to: string, subject: string, htmlBody: string }): Promise<void> {
  try {
    const { data, error } = await resend.emails.send({
      from: 'Weavers Loom <noreply@resend.dev>', // Note: This must be a verified domain in Resend.
      to: [to],
      subject: subject,
      html: htmlBody,
    });

    if (error) {
      console.error('Resend error:', error);
      throw new Error(`Failed to send email: ${error.message}`);
    }

    console.log('Email sent successfully:', data);
  } catch (error) {
    console.error('Error sending email:', error);
    // Re-throw the error to be caught by the calling function
    throw error;
  }
}
